.divContato ul{
    display: flex;
    justify-content: space-evenly;
}

.divContato li {
    list-style: none;
}

.divContato a {
color: #f39cab;
font-size: 2em;
}

.divContato a:hover {
color: #98C379;
}

#contato {
    padding: 2.5em 0;
    padding-top: 5em;
}

@media screen and (max-width: 390px) {
    #contato {
        width: 400px;
    }
}